<template>
  <el-container style="background-image:url('https://cdn.lovelive.cx/ms/web/bg.png_webimg')">
    <el-header style="text-shadow: 2px 2px 2px #000000; font-family: '楷体'">角色留言</el-header>
    <el-page-header @back="goBack" content="角色留言">
    </el-page-header>
    <el-main>
      <el-form @submit.prevent="submitComment">
        <el-form-item label="">
          <h1 style="color:orange">角色</h1>
          <el-select v-model="newComment.personName" style="width:400px">
            <el-option label="伊波杏树" value="伊波杏树"></el-option>
            <el-option label="逢田梨香子" value="逢田梨香子"></el-option>
            <el-option label="诹访奈奈香" value="诹访奈奈香"></el-option>
            <el-option label="小宫有纱" value="小宫有纱"></el-option>
            <el-option label="齐藤朱夏" value="齐藤朱夏"></el-option>
            <el-option label="小林爱香" value="小林爱香"></el-option>
            <el-option label="高槻加奈子" value="高槻加奈子"></el-option>
            <el-option label="铃木爱奈" value="铃木爱奈"></el-option>
            <el-option label="降幡爱" value="降幡爱"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="评论">
          <el-input type="textarea" v-model="newComment.commentText"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitComment">提交</el-button>
        </el-form-item>
      </el-form>
      <br>
      <h3>评论区</h3>
      <table cellspacing="0" align="center" class="comments-table">
        <tr>
          <th>角色</th>
          <th>评论</th>
          <th>时间</th>
        </tr>
        <tr v-for="comment in comments" :key="comment.id">
          <td>{{ comment.personName }}</td>
          <td>{{ comment.commentText }}</td>
          <td>{{ comment.commentTime }}</td>
        </tr>
      </table>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      newComment: {
        personName: '',
        commentText: '',
        commentTime: ''
      },
      comments: [],
    }
  },
  methods: {
    fetchData() {
      axios.get('http://localhost:8080/comments/all')
        .then(response => {
          if (response.data.code === 1) {
            this.comments = response.data.data;
          } 
        })
    },
    submitComment() {
      this.newComment.commentTime = new Date().toISOString();
      axios.post('http://localhost:8080/comments/add', this.newComment)
        .then(() => {
          this.fetchData();
        })
    },
    goBack(){
      this.$router.go(-1);
    }
  },
  mounted() {
    this.fetchData();
  }
}
</script>

<style>
.el-header {
  color: orange;
  text-align: center;
  padding: 10px;
  font-size: 40px;
}

.comments-table {
  width: 80%;
  margin: 0 auto;
}

.comments-table th, .comments-table td {
  /* border: 1px solid #ddd; */
  padding: 8px;
  text-align: left;
  word-wrap: break-word;
  max-width: 200px;
}

.comments-table th {
  /* background-color: #f2f2f2; */
  font-weight: bold;
}

</style>
